  .like-body {
    margin:0 auto;
    text-align: left;
  }
  
  /* i:hover {
    color:#666;
  } */
  
  .liked:before {
    font-family:fontawesome;
    content:'\f004';
    font-style:normal;
    color: red;
  }
  
  .unliked:before {
    font-family:fontawesome;
    content:'\f004';
    font-style:normal;
    color: white;
  }

  .comment:before{
    font-family:fontawesome;
    content:'\f075';
    font-style:normal;
    margin-left: 15px;
  }

  .comments {
      text-align: left;
  }

  .comment-input {
      width: 80%;
      padding: 10px 20px;
      margin: 10px 10px 10px 10px;
      box-sizing: border-box;

  }

  .send:before {
    font-family:fontawesome;
    content:'\f1d8';
    font-style:normal;
    margin-left: 20px;
    
    
  }

.comment-forms {
  margin-top: 190px;
