}

.comment-forms input {
  width: 25%;
  border-radius: 5px;
  background-color: rgb(41, 40, 40);
  border: 2px solid grey;
  padding: 12px 20px;
  margin-top: 10px;
  margin-right: -40px;
  margin-left: 50px;
  
}

.comment-forms button {
    background-color: rgb(228, 224, 224);
    width: 5%;
    border: none;
